<template>
  <div>
    <div class="headerDiv">
      <span><i class="el-icon-edit"></i>修改密码</span>
      <el-button type="info" size="small" @click="loginMethod" style="float: right">登录</el-button>
    </div>
    <el-form :model="resetForm" :rules="resetFormRules" ref="resetFormRef" status-icon label-width="100px" class="password_form">
      <el-form-item label="旧密码：" prop="password">
        <el-input type="password" v-model.trim="resetForm.password" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="新密码：" prop="newpwd">
        <el-input type="password" v-model.trim="resetForm.newpwd" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item label="确认密码：" prop="newpassword1">
        <el-input type="password" v-model.trim="resetForm.newpassword1" auto-complete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="passwordEnsure">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    data() {
      let validatePass = (rule, value, callback) => {
        const reg = /^(?![0-9_]+$)(?![a-zA-Z]+$)[0-9A-Za-z_]{8,16}$/;
        if (reg.test(value)) {
          return callback()
        }
        callback(new Error('请输入8-16位数字、字母组合的密码！'))
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === "") {
          callback(new Error("请再次输入密码！"));
        } else if (value !== this.resetForm.newpwd) {
          callback(new Error("两次输入密码不一致!"));
        } else {
          callback();
        }
      };
      return {
        loginName: '',
        resetForm: {
          newpassword1: "",
          password: "",
        },
        resetFormRules: {
          password: [
            {required: true, message: "请输入旧密码！", trigger: 'blur'}
          ],
          newpwd: [
            {required: true,validator: validatePass, trigger: 'blur'}
          ],
          newpassword1: [
            {required: true, validator: validatePass2, trigger: "blur"}
          ]
        }
      };
    },
    created() {
      this.loginName = this.$cookies.get('loginName');
    },
    methods: {
      passwordEnsure() {
        this.$refs.resetFormRef.validate(async valid => {
          if (!valid) return;
          const {data: res} = await this.$http.post(
            `user/updatePasswd?oldPass=${this.resetForm.password}`, {
              loginName: this.loginName, loginPass: this.resetForm.newpassword1
            }
          );
          if (res.code !== 0) return this.$message.error(res.msg);
          this.$message.success("修改成功,请重新登录！");
          setTimeout(() => {
            window.sessionStorage.clear();
            this.$cookies.remove('loginName');
            this.$router.push('/login')
          }, 3000);
        })
      },
      loginMethod(){
        window.sessionStorage.clear();
        this.$cookies.remove('loginName');
        this.$router.push('/login')
      }
    }
  }
</script>
<style lang="less" scoped>
  .headerDiv {
    width: 800px;
    border-bottom: 1px solid #ddd;
    height: 40px;
    padding: 150px 0 2px 0;
    font-size: 24px;
    margin: 0 auto;
    i {
      padding-right: 10px;
    }
  }

  .el-form {
    width: 500px;
    height: 350px;
    background-color: #fff;
    padding-top: 70px;
    margin: auto;
    .el-button {
      width: 100%
    }
  }
</style>

